<template>
  <!-- 
    1. 화면이 로딩 되면, 어떤 공간에서 필요한 데이터를 먼저 가져온다.

    2. 그 데이터를 가지고 왔다면, 어딘가에 저장한다 > 변수
      ( 변수 저장시 this.변수명 = 데이터 값)
      변수는 data 안에 설정
      - 가지고 온 데이터 + 저장한 데이터가 잘 나오는지 console 창에 꼭 확인하기!!!

    3. 반복해야 될 요소(div, p  등 전체 부모요소를 반복한다. )
      v-for="작명 in 변수명" :key=작명
      변수명에 있던 모든 데이터는 작명한 변수에 모두 들어간다.
      listItem[0].TITLE / listItem[1].MAIN_IMG_THUMB
      이렇게 생긴 값들이
      작명.TITLE / 작명.MAIN_IMG_THUMB 로 작성시 모두 적용된다.
   -->
   <!-- #eae6db,  -->
   <div class="w-full h-full bg-[#eae6db] px-[2%] pb-12 pt-14 box-border">
    <p class="font-extrabold text-[30px] tracking-widest text-center pb-10">부산광역시 부산축제 정보 서비스</p>
    <div class="max-w-7xl mx-auto flex flex-wrap gap-x-[1%]">
      <!-- 데이터 바인딩은 {{ }}, 단순출력은 그냥 타이핑 -->
      <ul class="flex flex-wrap justify-center bg-white rounded-md border box-border p-4 basis-full mb-4 gap-x-4 h-full">
        <li @click="cateName='전체'" class="hover:bg-black hover:text-white rounded-md px-1 cursor-pointer text-xs md:text-base basis-[20%] sm:basis-[15%] md:basis-[10%] lg:basis-[8%] text-center mb-2 sm:mb-0 border my-2 p-1" :class="cateName === '전체'">전체</li>        <!-- && 'font-extrabold text-orange-500' - 전체보기 원래 색 -->
        <!-- bg-gradient-to-r from-[#ff6e7f] to-[#bfe9ff] -->
        <li @click="cateName = e.GUGUN_NM" v-for="e in cateList" :key="e" class="hover:bg-black hover:text-white rounded-md cursor-pointer text-xs md:text-base basis-[20%] sm:basis-[15%] md:basis-[10%] lg:basis-[8%] text-center mb-[3px] sm:mb-0 border my-2 p-1" :class="cateName === e.GUGUN_NM && 'font-extrabold text-orange-500'">{{ e.GUGUN_NM }}</li>
      </ul>
      <!-- 반복문과 키값의 e는 작명 -->
      <div v-for="(e) in cateItem" :key="e" class="bg-white basis-full sm:basis-[49%] lg:basis-[32.5%] mb-4 box-border border rounded-md p-5">
        <!-- 반응형은 모바일 우선, 가로값은 full, sm:basis49%는 태블릿, lg는 PC --> 
        <!-- 데이터 바인딩시 :콜론 반드시 -->
        <h3 class="mb-4 sm:text-sm lg:text-lg text-center ">{{ e.TITLE }}</h3>
        <img :src="e.MAIN_IMG_THUMB" :alt="e.TITLE" :title="e.TITLE" class="w-full mb-6">
        <ul>
          <li>전화번호 : {{ e.CNTCT_TEL }}</li>
          <li v-if="e.USAGE_AMOUNT === ''">이용요금 : 무료</li>
          <li v-else>이용 요금 : {{ e.USAGE_AMOUNT }}</li>
          <li class="my-2 tracking-wide" v-if="e.TRFC_INFO">교통편 : {{ e.TRFC_INFO }}</li>
          <li class="my-2 tracking-wide" v-else>교통편 : 등록되지 않음</li>
          <!-- 콘솔창에 있는 대소문자 그대로 써줄 것 -->
          <li class="mb-2 font-bold text-[18px]"><button v-if="e.HOMEPAGE_URL !== ''" @click="open_page(e.HOMEPAGE_URL)">홈페이지</button></li>
          <!-- button v-if="e.HOMEPAGE_URL"와 동일  -->
        </ul>
        <router-link class="mb-0 text-[18px]" :to="'/detail/'+(e.UC_SEQ)" @click="ResultList(e.UC_SEQ)">상세 내용</router-link>  
      </div>
      <div class="basis-full bg-white flex justify-around">
        <ul class="flex justify-center">
          <!-- 문법의 의미상 this.index와 동일 -->
          <li v-for="e in totalCount / pageList" :key="e" class="cursor-pointer" :class="e === page && 'font-extrabold text-orange-500'" @click="page = e; getItem()">{{ e }}</li>
        </ul>
      </div>
      <!-- {{ page }} -->
    </div>
  </div>
</template>

<script>

  // https://apis.data.go.kr/6260000/FestivalService/getFestivalKr?serviceKey=Fag5ltUiVOMqlOb7r2X7nJWRY4JRoCTu8jtWwcjMob9oY1L%2Ffo3uf1%2FTc62pFVIIpFpxdaQdHC%2BhSM87yRcpBg%3D%3D&pageNo=1&numOfRows=10&resultType=json
import axios from 'axios'
export default {
  name: 'App',
  components: {
    
  },
  data() {
    return {
      pageList: 30,
      listItem: [],
      page: 1,
      totalCount: 0,
      cateName : "전체"
    }
  },
  methods:{
    ResultList(e){
      return this.listItem.filter((data)=>{
        if(data.UC_SEQ === e){
          return this.session(data)
          // return console.log(data)
        }
      })
    },
    // Local과 달리 session스토리지는 브라우저가 종료되면 저장된 데이터가 사라짐
    session(data){
      sessionStorage.setItem('data', JSON.stringify(data)); 
      // JSON 문법으로 저장하는 방식이며, 위에 ResultList(e)의 데이터 출력문과의 순서는 상관없지만 메소드 안에 있어야함;
    },
    open_page(url){
      window.open(url);
    },
    // 페이지 리스트
      getItem: function(){
      axios.get(`https://apis.data.go.kr/6260000/FestivalService/getFestivalKr?serviceKey=Fag5ltUiVOMqlOb7r2X7nJWRY4JRoCTu8jtWwcjMob9oY1L%2Ffo3uf1%2FTc62pFVIIpFpxdaQdHC%2BhSM87yRcpBg%3D%3D&pageNo=${this.page}&numOfRows=10&resultType=json`)
      
      .then((res)=>{  
        this.listItem = res.data.getFestivalKr.item;
      })
      .catch((error)=>{ // 실패시 에러 출력
        console.log(error)
      })
    }
  },
  
  mounted(){
    console.log("Create 이후 실행")
  },
  
  computed: { // 실시간 연산자 코드
   cateList(){
    return this.listItem.filter((item,i)=>{
      return(
        this.listItem.findIndex((item2)=>{
          return item.GUGUN_NM === item2.GUGUN_NM
        }) === i
      )
    })
   },
   cateItem(){
    return this.listItem.filter((data)=>{
      // 전체 버튼 클릭 시 컨텐츠 다 표시
      if(this.cateName !== '전체'){
        return data.GUGUN_NM === this.cateName
      }else{
       return data.GUGUN_NM 
      }
    })
  }
},
created(){ // 데이터가 먼저 들어와서 저장되는 곳, html과 달리 아래에서 위로 실행
    console.log("먼저 실행") 

    axios.get(`https://apis.data.go.kr/6260000/FestivalService/getFestivalKr?serviceKey=Fag5ltUiVOMqlOb7r2X7nJWRY4JRoCTu8jtWwcjMob9oY1L%2Ffo3uf1%2FTc62pFVIIpFpxdaQdHC%2BhSM87yRcpBg%3D%3D&pageNo=1&numOfRows=15&resultType=json`)
    
    .then((res)=>{  // res = resposive줄임말
      this.listItem = res.data.getFestivalKr.item; // this는 반드시 붙어야함
      console.log(res)
    })
    .catch((error)=>{ // 실패시 에러 출력
      console.log(error)
    })
  }
  // computed - 탬플릿 내 값을 연산할 때 사용 - 같은 값을 때는 연산 하지않는다.
  // computed - 사용시 return 꼭 사용!!
  // watch - 같은 값이어도 다시 연산
  // ※ 값이 바뀔일이 없다면 computed 사용
  // created - 인스턴스가 작성된 후 동기적으로 호출
  // mounted - 부모, 자식 관계의 컴포넌트가 랜더링 될 때 created 다음으로 호출 > 자식 / 부모 순으로 실행
  // * 뷰는 create를 생성하고 DOM에 부착(mounted) 업데이트(update) 없어지는(destroy) 4가지      과정을 거친다.
  
}

</script>

<style>

</style>